<template>
  <!-- 订单详情对话框 -->
  <DetailForm ref="detailFormRef" @refresh="refreshData"/>
  <!-- 使用说明 -->

  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-card class="search-card" shadow="never">
        
        <el-form :model="searchForm" label-width="100px" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="订单号">
                <el-input 
                  v-model="searchForm.order_no" 
                  placeholder="请输入订单号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="商户订单号">
                <el-input 
                  v-model="searchForm.merchant_order_no" 
                  placeholder="请输入商户订单号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="商户">
                <el-select 
                  v-model="searchForm.merchant_id" 
                  placeholder="请选择商户" 
                  clearable
                  filterable
                  style="width: 100%"
                  v-if="!userInfo?.is_merchant_admin"
                >
                  <el-option 
                    v-for="merchant in merchantList" 
                    :key="merchant.id" 
                    :label="merchant.merchant_name" 
                    :value="merchant.id" 
                  />
                </el-select>
                <div v-else style="color: #999; font-size: 12px;">
                  商户用户无需选择商户
                </div>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="订单状态">
                <el-select 
                  v-model="searchForm.status" 
                  placeholder="请选择订单状态" 
                  clearable
                  style="width: 100%"
                >
                  <el-option label="待支付" :value="1" />
                  <el-option label="支付中" :value="2" />
                  <el-option label="支付成功" :value="3" />
                  <el-option label="支付失败" :value="4" />
                  <el-option label="已退款" :value="5" />
                  <el-option label="已关闭" :value="6" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="时间范围">
                <el-date-picker
                  v-model="searchForm.timeRange"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  format="YYYY-MM-DD HH:mm:ss"
                  value-format="YYYY-MM-DD HH:mm:ss"
                  style="width: 100%"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <div class="search-buttons">
                  <el-button type="primary" @click="handleSearch" :loading="searchLoading">
                    <el-icon><Search /></el-icon>
                    搜索
                  </el-button>
                  <el-button @click="handleReset">
                    <el-icon><Refresh /></el-icon>
                    重置
                  </el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </transition>
  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新按钮 -->
    <el-button v-permission="'order:list'" :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button v-permission="'order:resend'" type="primary" :disabled="!hasSelectedRows" @click="handleBatchReissue">补单</el-button>
    <el-button v-permission="'order:callback'" type="primary" :disabled="!hasSelectedRows" @click="handleBatchCallback">回调</el-button>

    <!-- 统计数据 -->
    <div class="table-statistics">
      <span class="stat-item stat-highlight">累计订单数: <b class="stat-number">{{ statistics?.total_orders || 0 }}</b></span>
      <span class="stat-item stat-highlight">订单总金额: <b class="stat-number">¥{{ formatAmount(statistics?.total_amount || 0) }}</b></span>
      <span class="stat-item stat-success">已付订单数: <b class="stat-number">{{ statistics?.paid_orders || 0 }}</b></span>
      <span class="stat-item stat-success">已付总金额: <b class="stat-number">¥{{ formatAmount(statistics?.paid_amount || 0) }}</b></span>
      <span class="stat-item stat-warning">未付订单数: <b class="stat-number">{{ statistics?.unpaid_orders || 0 }}</b></span>
      <span class="stat-item stat-warning">未付总金额: <b class="stat-number">¥{{ formatAmount(statistics?.unpaid_amount || 0) }}</b></span>
      <span class="stat-item stat-success">成功率: <b class="stat-number">{{ formatPercentage(statistics?.success_rate || 0) }}%</b></span>
    </div>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="40"/>

      <!-- 平台单号 -->
      <el-table-column prop="order_no" label="平台单号" align="center" min-width="200"/>
      
      <!-- 商户单号 -->
      <el-table-column prop="merchant_order_no" label="商户单号" align="center" min-width="167"/>
      
      <!-- 订单金额 -->
      <el-table-column prop="amount" label="订单金额" align="center" min-width="100">
        <template #default="{ row }">
          ¥{{ (row.amount / 100).toFixed(2) }}
        </template>
      </el-table-column>
      
      <!-- 支付状态 -->
      <el-table-column label="支付状态" align="center" min-width="100">
        <template #default="{ row }">
          <el-tag :type="getStatusTagType(row.status) as any">
            {{ getStatusText(row.status) }}
          </el-tag>
        </template>
      </el-table-column>
      
      <!-- 回调状态 -->
      <el-table-column prop="notify_status" label="回调状态" align="center" min-width="100">
        <template #default="{ row }">
          <el-tag :type="getNotifyStatusTagType(row.notify_status) as any" size="small">
            {{ getNotifyStatusText(row.notify_status) }}
          </el-tag>
        </template>
      </el-table-column>
      
      <!-- 商户名称 -->
      <el-table-column prop="merchant.merchant_name" label="商户名称" align="center" min-width="150"/>
      
      <!-- 通道名称 -->
      <el-table-column label="通道名称" align="center" min-width="120">
        <template #default="{ row }">
          <span v-if="row.channel">
            {{ row.channel.channel_name || '百亿支付' }}
          </span>
          <span v-else>
            百亿支付
          </span>
        </template>
      </el-table-column>
      
      <!-- 支付时间 -->
      <el-table-column prop="paid_time" label="支付时间" align="center" min-width="160"/>

      <el-table-column label="操作" align="center" fixed="right" min-width="120">
        <template #default="scope">
          <el-dropdown trigger="click" @command="(command) => handleAction(command, scope.row)">
            <el-button size="small" type="primary">
              操作<el-icon class="el-icon--right"><arrow-down /></el-icon>
            </el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item v-permission="'order:detail'" command="detail">订单详情</el-dropdown-item>
                <el-dropdown-item v-permission="'order:query'" command="query">查单</el-dropdown-item>
                <el-dropdown-item v-permission="'order:refund'" command="refund" v-if="scope.row.status === 3">退款</el-dropdown-item>
                <el-dropdown-item v-permission="'order:resend'" command="resend" v-if="[4, 6].includes(scope.row.status)">补单</el-dropdown-item>
                <el-dropdown-item v-permission="'order:callback'" command="callback" v-if="scope.row.status === 3">回调</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
  </div>
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>

</template>

<style scoped>
.search-card {
  margin-bottom: 0;
  border: 1px solid #e4e7ed;
  border-radius: 8px;
}

.search-card :deep(.el-card__header) {
  padding: 12px 20px;
  border-bottom: 1px solid #f0f0f0;
}

