  background: #fafafa;
  padding: 0;
  border-radius: 8px;
  margin-bottom: 0;
}
</style>

<script lang="ts" setup>
import { Refresh, Search, Close, ArrowDown } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent, nextTick } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入详情页面
const DetailForm = defineAsyncComponent(() => import('./detail.vue'));
// 导入详情页面
const detailFormRef = ref(null);
const openDetailFrom = (row = {}) => {
  console.log(row);
  // 使用 nextTick 确保组件已经挂载
  nextTick(() => {
    if (detailFormRef.value && (detailFormRef.value as any).open) {
      (detailFormRef.value as any).open({ ...row}); // 传递 `row` 作为参数
    } else {
      console.error("DetailForm 组件未正确挂载！");
    }
  });
};
//
// // 使用表格插件来管理表格的状态、数据等
// const {
//   handleSelectionChange,
//   hasSelectedRows,
//   isVisible: showSearch,
//   toggleVisibility: toggleSearch,
//   loadData,
//   data,
//   refreshData,
//   handleSizeChange,
//   handleCurrentChange,
//   searchParams,
//   resetSearchParams
// } = useTableDataPlugin("/api/v1/admin/order");
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/order");

// 设置搜索框默认展开
showSearch.value = true;
// 确保搜索参数有正确的结构
if (!(searchParams.value as any).search) {
  (searchParams.value as any).search = {};
}

// 初始化搜索参数
const initSearchParams = () => {
  if (!(searchParams.value as any).search) {
    (searchParams.value as any).search = {
      order_no: '',
      merchant_order_no: '',
      merchant_id: '',
      status: '',
      start_time: '',
      end_time: ''
    };
  }
};

// 搜索表单数据
const searchForm = ref({
  order_no: '',
  merchant_order_no: '',
  merchant_id: '',
  status: '',
  timeRange: null as [Date, Date] | null
});

// 商户列表
const merchantList = ref<any[]>([]);

// 用户信息
const userInfo = ref<any>(null);

// 搜索加载状态
const searchLoading = ref(false);

// 统计数据
const statistics = ref<any>(null);

// 格式化金额
const formatAmount = (amount: number | string) => {
  if (!amount) return '0.00';
  return Number(amount).toFixed(2);
};

// 格式化百分比
const formatPercentage = (rate: number | string) => {
  if (!rate) return '0.00';
  return Number(rate).toFixed(2);
};

// 获取用户信息
const loadUserInfo = async () => {
  try {
    const res = await request.get('/api/v1/admin/user/info');
    console.log('用户信息API响应:', res);
    if ((res as any).code === 200) {
      userInfo.value = (res as any).data;
      console.log('用户信息:', userInfo.value);
      console.log('是否为商户管理员:', userInfo.value.is_merchant_admin);
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取商户列表
const loadMerchantList = async () => {
  try {
    const res = await request.get('/api/v1/admin/merchant', {
      params: {
        page: 1,
        page_size: 1000, // 获取所有商户
        search: JSON.stringify({})
      }
    });
    console.log('商户API响应:', res);
    if ((res as any).code === 200) {
      merchantList.value = (res as any).data.data || [];
      console.log('商户列表:', merchantList.value);
    } else {
      console.log('商户API响应错误:', res);
    }
  } catch (error) {
    console.error('获取商户列表失败:', error);
    merchantList.value = [];
  }
};

// 获取统计数据
const loadStatistics = async (searchParams: any = {}) => {
  try {
    // 构建查询参数
    const params: any = {};
    
    // 如果有搜索参数，传递给统计接口
    if (searchParams && Object.keys(searchParams).length > 0) {
      // 处理时间范围
      if (searchParams.timeRange && searchParams.timeRange.length === 2) {
        params.start_time = searchParams.timeRange[0];
        params.end_time = searchParams.timeRange[1];
      }
      
      // 传递其他搜索参数
      if (searchParams.order_no) params.order_no = searchParams.order_no;
      if (searchParams.merchant_order_no) params.merchant_order_no = searchParams.merchant_order_no;
      if (searchParams.merchant_id) params.merchant_id = searchParams.merchant_id;
      if (searchParams.status) params.status = searchParams.status;
      if (searchParams.start_time) params.start_time = searchParams.start_time;
      if (searchParams.end_time) params.end_time = searchParams.end_time;
    }
    
    console.log('统计接口请求参数:', params);
    
    const res = await request.get('/api/v1/admin/order/statistics', { params });
    if ((res as any).code === 200) {
      statistics.value = (res as any).data;
      console.log('统计数据更新:', statistics.value);
    }
  } catch (error) {
    console.error('获取统计数据失败:', error);
    // 如果API不存在，使用模拟数据
    statistics.value = {
      total_orders: 0,
      total_amount: 0,
      paid_orders: 0,
      paid_amount: 0,
      merchant_income: 0,
      unpaid_orders: 0,
      unpaid_amount: 0,
      success_rate: 0
    };
  }
};


// 处理搜索
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // 处理时间范围
    let searchData: any = { ...searchForm.value };
    if (searchData.timeRange && searchData.timeRange.length === 2) {
      searchData.start_time = searchData.timeRange[0];
      searchData.end_time = searchData.timeRange[1];
    }
    delete searchData.timeRange;
    delete searchData.quickTime;
    
    // 将搜索表单数据同步到searchParams
    (searchParams.value as any).search = searchData;
    
    // 先刷新数据，再更新统计（传递搜索参数）
    await refreshData();
    await loadStatistics(searchData);
  } catch (error) {
    console.error('搜索失败:', error);
    ElMessage.error('搜索失败，请重试');
  } finally {
    searchLoading.value = false;
  }
};

// 处理重置
const handleReset = async () => {
  searchForm.value = {
    order_no: '',
    merchant_order_no: '',
    merchant_id: '',
    status: '',
    timeRange: null
  };
  (searchParams.value as any).search = { ...searchForm.value };
  
  // 重置时也更新统计数据（不传递搜索参数，获取全部数据统计）
  await refreshData();
  await loadStatistics();
};

// 在组件挂载时初始化搜索参数和加载数据
onMounted(() => {
  initSearchParams();
  loadData();
  loadStatistics();
  loadUserInfo();
  loadMerchantList();
  
  // 调试信息
  console.log('data.admin:', data.value.admin);
  console.log('userInfo:', userInfo.value);
  console.log('merchantList:', merchantList.value);
  console.log('isMerchantAdmin:', userInfo.value?.is_merchant_admin);
});

// 获取订单状态文本
const getStatusText = (status: number) => {
  const statusMap: { [key: number]: string } = {
    1: '待支付',
    2: '支付中',
    3: '支付成功',
    4: '支付失败',
    5: '已退款',
    6: '已关闭'
  };
  return statusMap[status] || '未知状态';
};

// 获取订单状态标签类型
const getStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    1: 'warning',
    2: 'primary',
    3: 'success',
    4: 'danger',
    5: 'info',
    6: 'info'
  };
  return typeMap[status] || 'info';
};

// 获取通知状态文本
const getNotifyStatusText = (status: number) => {
  const statusMap: { [key: number]: string } = {
    0: '未通知',
    1: '通知成功',
    2: '通知失败'
  };
  return statusMap[status] || '未知状态';
};

// 获取通知状态标签类型
const getNotifyStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    0: 'warning',
    1: 'success',
    2: 'danger'
  };
  return typeMap[status] || 'info';
};

// 处理退款
const handleRefund = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要退款订单 ${row.order_no} 吗？`,
      '确认退款',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );

    // 调用退款API
    const { refundOrder } = await import('@/api/orderManagement.ts');
    const res = await refundOrder('/api/v1/admin/order/refund', {
      order_id: row.id,
      reason: '管理员退款'
    });

    if (res.status) {
      ElMessage.success('退款申请已提交');
      refreshData();
    } else {
      ElMessage.error((res as any).message);
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('退款申请失败');
    }
  }
};

// 处理补单
const handleReissue = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要补单 ${row.order_no} 吗？`,
      '确认补单',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用补单API
    const result = await request.post('/api/v1/admin/order/reissue', {
      ids: [row.id]
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('补单成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('补单失败');
  }
};

// 处理回调
const handleCallback = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要重新回调 ${row.order_no} 吗？`,
      '确认回调',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用回调API
    const result = await request.post('/api/v1/admin/order/callback', {
      ids: [row.id]
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('回调成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('回调失败');
  }
};

// 批量补单
const handleBatchReissue = async () => {
  if (!hasSelectedRows.value) {
    ElMessage.warning('请选择要补单的订单');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      `确定要批量补单选中的订单吗？`,
      '确认批量补单',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用补单API（统一使用ids参数）
    const result = await request.post('/api/v1/admin/order/reissue', {
      ids: (data.value as any).selectedRows?.map((row: any) => row.id) || []
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('批量补单成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('批量补单失败');
  }
};

// 批量回调
const handleBatchCallback = async () => {
  if (!hasSelectedRows.value) {
    ElMessage.warning('请选择要回调的订单');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      `确定要批量回调选中的订单吗？`,
      '确认批量回调',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );
    
    // 调用回调API（统一使用ids参数）
    const result = await request.post('/api/v1/admin/order/callback', {
      ids: (data.value as any).selectedRows?.map((row: any) => row.id) || []
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('批量回调成功');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('批量回调失败');
  }
};

// 处理下拉操作
const handleAction = (command: string, row: any) => {
  switch (command) {
    case 'detail':
      openDetailFrom(row);
      break;
    case 'query':
      handleQuery(row);
      break;
    case 'refund':
      handleRefund(row);
      break;
    case 'resend':
      handleReissue(row);
      break;
    case 'callback':
      handleCallback(row);
      break;
    default:
      console.warn('未知操作:', command);
  }
};

// 处理查单
const handleQuery = async (row: any) => {
  try {
    // 调用查单API
    const result = await request.post('/api/v1/admin/order/query', {
      order_no: row.order_no
    });
    
    if ((result as any).code === 200) {
      const queryData = (result as any).data;
      
      // 显示查单结果
      let message = `📋 订单信息\n`;
      message += `═══════════════════════════════════════════════════════════\n`;
      
      // 订单基本信息
      message += `🔢 订单号：${queryData.order_no}\n`;
      message += `📊 当前状态：${getStatusText(queryData.status)}\n`;
      message += `💰 订单金额：¥${(queryData.amount / 100).toFixed(2)}\n`;
      message += `📅 创建时间：${new Date(queryData.created_at).toLocaleString()}\n\n`;
      
      // 支付通道查询结果
      message += `🔍 支付通道查询结果\n`;
      message += `───────────────────────────────────────────────────────────\n`;
      
      if (queryData.query_success) {
        message += `✅ 查询状态：成功\n`;
        
        // 显示当前状态（优先显示统一状态，否则显示通道消息）
        if (queryData.query_result && queryData.query_result.unified_status_text) {
          message += `📊 当前状态：${queryData.query_result.unified_status_text}\n`;
        } else {
          message += `📊 当前状态：${queryData.query_message}\n`;
        }
        
        // 显示统一状态信息
        if (queryData.query_result && queryData.query_result.unified_status_text) {
          message += `\n📋 状态详情：\n`;
          message += `───────────────────────────────────────────────────────────\n`;
          message += `🎯 统一状态：${queryData.query_result.unified_status_text}\n`;
          message += `📝 原始状态：${queryData.query_result.original_status || '未知'}\n`;
          message += `📊 状态字段：${queryData.query_result.original_status_field || 'status'}\n`;
          message += `🏢 供货商类型：${queryData.query_result.supplier_type || '未知'}\n`;
        }
        
        if (queryData.query_result && Object.keys(queryData.query_result).length > 0) {
          // message += `\n📄 通道返回数据：\n`;
          // message += `───────────────────────────────────────────────────────────\n`;
          //
          // // 格式化JSON数据，确保完整显示
          // const jsonString = JSON.stringify(queryData.query_result, null, 2);
          // // 如果JSON数据太长，进行适当的分段显示
          // if (jsonString.length > 2000) {
          //   message += `数据较大，共 ${jsonString.length} 字符\n`;
          //   message += `前1000字符：\n${jsonString.substring(0, 1000)}...\n`;
          //   message += `\n后1000字符：\n...${jsonString.substring(jsonString.length - 1000)}\n`;
          //   message += `\n完整数据已输出到控制台，请按F12查看\n`;
          //   console.log('完整查单结果数据：', queryData.query_result);
          // } else {
          //   message += `${jsonString}\n`;
          // }
        } else {
          message += `\n📄 通道返回数据：无数据\n`;
        }
      } else {
        message += `❌ 查询状态：失败\n`;
        message += `⚠️  错误信息：${queryData.query_message}\n`;
      }
      
      message += `\n═══════════════════════════════════════════════════════════`;
      
      // 使用HTML格式显示，支持更好的样式控制
      const htmlMessage = message.replace(/\n/g, '<br>').replace(/`([^`]+)`/g, '<code>$1</code>');
      
      ElMessageBox.alert(htmlMessage, '🔍 查单结果', {
        confirmButtonText: '确定',
        type: queryData.query_success ? 'success' : 'warning',
        dangerouslyUseHTMLString: true,
        customStyle: {
          'max-width': '900px',
          'max-height': '700px',
          'overflow': 'auto',
          'font-family': 'monospace',
          'line-height': '1.6',
          'white-space': 'pre-wrap',
          'word-break': 'break-all'
        }
      });
    } else {
      console.error('查单API返回错误：', result);
      ElMessage.error((result as any).msg || '查单失败');
    }
  } catch (error) {
    console.error('查单请求异常：', error);
    ElMessage.error('查单失败：' + ((error as any).message || '网络错误'));
  }
};

</script>
